<template>
    <div class="send-message">
        <Table class="message-table" stripe :columns="messageHeader" :data="messageData"></Table>
        <div class="tag-modal" v-if="modalShow">
            <h3>请选择要发送的标签</h3>
            <br>
            <RadioGroup v-model="tagId">
                <Radio v-for="(tag, index) in tagList" :key="index" :label="tag.tagId">{{tag.name}}</Radio>
            </RadioGroup>
            <br>
            <Button class="btn" type="warning" @click="cancel">取消</Button>
            <Button class="btn" type="success" @click="submit">确定</Button>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            modalShow: false,
            tagList: [
                {tagId: 0, name: '男'},
                {tagId: 1, name: '女'},
                {tagId: 2, name: '未知'},
            ],
            id: "",
            tagId: 0,
            mediaId: "",
            messageData: [
                {
                    media_id: '12'
                }
            ],
            messageHeader: [
                {
                    key: "media_id",
                    title: "media_id",
                    align: "center",
                    width: 100
                },
                {
                    key: "name",
                    title: "name",
                    align: "center"
                },
                {
                    key: "url",
                    title: "url",
                    align: "center",
                    width: 80
                },
                {
                    key: "update_time",
                    title: "update_time",
                    align: "center"
                },
                {
                    title: "图文内容",
                    align: "center",
                    render: (h, params) => {
                        let content = params.row.content.news_item[0].content;
                        return h('span', {
                            props: {}
                        }, content)
                    }
                },
                {
                    title: "图文图片",
                    align: "center",
                    render: (h, params) => {
                        let url = params.row.content.news_item[0].url;
                        return h('span', {
                            props: {}
                        }, url)
                    }
                },
                {
                    title: "Action",
                    align: "center",
                    render: (h, params) => {
                        let that = this;
                        return h("div", [
                            h(
                                "Button",
                                {
                                    style: {
                                        marginRight: "10px"
                                    },
                                    props: {
                                        type: "primary",
                                        size: "small"
                                    },
                                    on: {
                                        click() {
                                            that.modalShow = true;
                                            that.id = params.row._id;
                                            that.mediaId = params.row.mediaId
                                            // that.tagList();
                                        }
                                    }
                                },
                                "发送消息"
                            )
                        ]);
                    }
                }
            ]
        };
    },
    mounted() {
        // this.showDataList();
    },
    methods: {
        showDataList() {
            this.$axios
                .get("/material/show", { params: { code: this.$route.params } })
                .then(res => {
                    this.messageData = res.data.data;
                });
        },
        getTagList() {
            this.$axios.get("/material/tag").then(res => {
                this.tagList = res.data.data;
            });
        },
        cancel() {
            this.modalShow = false;
        },
        submit() {
            this.$axios.get("/material/send", {
                params: {
                    id: this.id,
                    tagId: this.tagId,
                    mediaId: this.mediaId
                }
            }).then(res => {
                this.$Message.info(res.data.data)
            })
        }
    }
};
</script>

<style scoped>

.message-table {
    width: 100%;
    clear: both;
}
.tag-modal {
    width: 300px;
    height: 180px;
    padding: 30px;
    position: fixed;
    top: 50%;
    left: 50%;
    margin-left: -165px;
    margin-top: -105px;
    background: rgba(0, 0, 0, .7);
    z-index: 100000;
    color: #fff;
}
.tag-modal .btn {
    margin-top: 30px;
    margin-right: 20px;
}
</style>

